


function crearGaleria(){
    const galeria = document.querySelector('.galeria-imagenes'); //construimos un objeto apartir del el elemento ul que es el que seleccionamos
    for( let i = 1; i <=12; i++ ){
        const imagen = document.createElement('IMG'); //IMG es el tag del elemento a crear
        
        imagen.src = `/build/img/thumb/${i}.webp`; //elegimos todas las imagenes una por una
        
        imagen.dataset.imagenId = i; //le añadimos el atributo data-imagen-id 
        
        //Añadimos la funcion de mostrarImagen
        imagen.onclick = mostrarImagen;
        
        const lista = document.createElement('LI'); //añadimos el li
        lista.appendChild(imagen); //añadimos imagenes a los li
        galeria.appendChild(lista); //añadimos las imagenes a la ul

    }
}

function mostrarImagen(e){
    const id = parseInt(e.target.dataset.imagenId); //transformamos el id de formato de strinmg a numerb
    
    //Generar la imagen
    const imagen = document.createElement('IMG');
    imagen.src = imagen.src = `/build/img/grande/${id}.webp`;
    
    const overlay = document.createElement('DIV');
    overlay.appendChild(imagen);
    overlay.classList.add('overlay');
    //Mostrar en el HTML

    const body = document.querySelector('body');
    body.appendChild(overlay);

    //boton para cerrar la imagen

    const cerrarImagen = document.createElement('P');
    cerrarImagen.textContent = 'X';
    cerrarImagen.classList.add('btn-cerrar');
    overlay.appendChild(cerrarImagen);
    body.classList.add('fijar-body');
    //Cuando se presiona se cierra la imagen

    cerrarImagen.onclick = function(){
        overlay.remove();
        body.classList.remove('fijar-body');  
    };

    overlay.onclick = function(){
        overlay.remove();
        body.classList.remove('fijar-body');
    
    };


}




document.addEventListener('DOMContentLoaded',crearGaleria);


